* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin-top: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

header {
  width: 100vw;
  background-color: #6a5acd;
  display: flex;
}

.search-form {
  width: 300px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.info {
  display: flex;
}

.gallery {
  width: 100vw;
  grid-template-columns: repeat(4, 1fr);
  margin: 15px 0 15px 15px;
  display: grid;
}

.photo-card {
  border: 1px solid #a16666;
  border-radius: 4px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  margin-right: 15px;
  padding-bottom: 10px;
}

.photo-card img {
  width: 100%;
  object-fit: cover;
  height: 175px;
}

.info-item {
  text-align: center;
  margin-left: 5px;
}

.info-item:last-child {
  margin-left: 0;
}

nav {
  padding: 10px;
  display: flex;
}

.load-more {
  height: 40px;
  width: 150px;
  color: #fff;
  background-color: #6a5acd;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  font-size: 20px;
}

.search-form input, .submit {
  font-size: 20px;
}

